import React, { Component } from 'react'
import {Spring} from 'react-spring/renderprops';
export class Component2 extends Component {

    render() {
        return (
            <Spring from={{opacity:0}} to={{opacity:1}} config={{delay:1000,duration:1000}}>
            {
                props=>(
                    <div style={props}>
                        <div style={style2}>
                            <h1>Component1</h1>
                            <p>lorel20</p>
                            <button style={btn}  onClick={this.props.toggle}>Component3</button>
                        </div>
                    </div>
                )
        }
        </Spring>
        )
    }
}
const style2={
    margin:"auto",
    width:"50%",
    background:'slateblue',
    color:'#ffffff',
    padding:'1.5rem'
}
const btn={
    display:'block',
    background:'black',
    padding:'1rem',
    margin:'10px 0',
    color:'#ffffff',
    border:'none'
}

export default Component2
