import React  from 'react';
import {Spring} from 'react-spring/renderprops';
 export default function Component1() {
    return (
        <Spring from={{opacity:0}} to={{opacity:1}} config={{duration:500}}>
            {
                props=>(
                    <div style={props}>
                        <div style={style1}>
                            <h1>Component1</h1>
                            <p>lorel20
                            </p>
                        </div>
                    </div>
                )
        }
        </Spring>
            
        )
}
const style1={
    margin:"auto",
    width:"50%",
    background:'steelblue',
    color:'#ffffff',
    padding:'1.5rem'
}
